.player{
    background: #282828;
    position: fixed;
    color:white;
    height:90px;
    padding:16px;
    width:100%;
    bottom:0;
    border: solid #88f5aa;
    border-width: 1PX 0 0 0
}

.progressLoc {
	background-color: #a0a0a0;
    height: inherit;
    width: 0;
    border-radius: 2px;
}

#nowPlayingBarContainer {
	width: 100%;
    background-color: #282828;
    bottom: 0;
    position: fixed;
    min-width: 620px;
    left:0px;
    z-index: 1;
}

#nowPlayingBar {
	display: flex;
    height: 90px;
    padding: 16px;
    box-sizing: border-box;
}

#nowPlayingLeft,
#nowPlayingRight {
	width: 30%;
    min-width: 180px;
    float:left;
}

#nowPlayingRight {
	position: relative;
    margin-top: 16px;
}

#nowPlayingCenter {
	width: 40%;
    max-width: 700px;
    display: flex;
    flex-direction: column;
    align-items: center;
    float:left;
}

#nowPlayingBar .content {
	width: 100%;
	height: 57px;
}
.controlButton{
    background-color: transparent;
    border: 0px;
}
.playerControls .buttons {
	margin: 0 auto;
    display: table;

}

.controlButton.play img,
.controlButton.pause img{
	width: 32px;
	height: 32px;
}



.controlButton {
	background-color: transparent;
    border: none;
    vertical-align: middle;
}

.controlButton img {
	width: 20px;
    height: 20px;
}



.controlButton:hover {
	cursor: pointer;
}

.progressTime {
	color: #a0a0a0;
    font-size: 11px;
    min-width: 40px;
    text-align: center;
}


.volumeBar {
	width: 180px;
    position: absolute;
    right: 0;
}

.volumeBar .progressBar {
	width: 125px;
}


.playbackBar {
	display: flex;
}

.progressBar {
	width: 100%;
    height: 12px;
    display: inline-flex;
    cursor: pointer;
}

.progressBarBg {
	background-color: #404040;
    height: 4px;
    width: 490px;
    border-radius: 2px;
    
}

.songVolProgress {
	background-color: #a0a0a0;
    height: 4px;
    width: 0;
    border-radius: 2px;

}

.playbackBar .progressBar {
	margin-top: 3px;
}

#nowPlayingLeft .albumArtwork {

    max-width: 60px;
    margin-right: 15px;
    float: left;
    background-size: cover;
}

#nowPlayingLeft .trackInfo {
	display: table;
}

#nowPlayingLeft .trackInfo .trackName {
	margin: 6px 0;
    display: inline-block;
    width: 100%;
}

#nowPlayingLeft .trackInfo .artistName span {
	font-size: 12px;
    color: #a0a0a0;
}
